












































import { Component, Vue, Watch } from "vue-property-decorator";
import {
  GetUser,
  User,
  Event,
  GetUserEvents,
  GetEvents,
  GetUsers,
} from "../modules/ApiModel";
import UserInfo from "../components/UserInfo.vue";
import Events from "../components/Events.vue";
import BarGraph from "../components/BarGraph.vue";
import * as Stats from "../modules/Stats";

@Component({
  components: {
    UserInfo,
    Events,
    BarGraph,
  },
})
export default class StatsPage extends Vue {
  eventCount = 0;
  customerCount = 0;

  users: User[] = [];
  events: Event[] = [];
  loaded = false;
  value = "";

  async created() {
    this.LoadData();
  }

  async LoadData() {
    Promise.all([GetUsers(), GetEvents()]).then(([users, events]) => {
      this.events = events;
      this.eventCount = this.events.length;
      this.customerCount = this.events
        .map((a) => a.customerCount)
        .reduce((a, b) => a + b);
      this.users = users;
      this.loaded = true;
    });
  }

  get weekDayCounts(): {} {
    return Stats.weekDayCounts(this.events);
  }

  get monthsCounts(): {} {
    return Stats.monthsCounts(this.events);
  }

  get userEvents(): {} {
    return Stats.userEvents(this.events, this.users);
  }
}
